.topbar {
    @include clear;
    width: 100%;
    .languages {
        @include clear;
        float: left;
        .lang {
            float: left;
            text-transform: uppercase; }
        .switch {
            float: left;
            // margin: 0 0.5rem
            #lang_switch {
                display: none; }
            label {
                background: $clouds;
                display: inline-block;
                border-radius: rem(10px);
                text-indent: -99999px;
                height: rem(20px);
                width: rem(35px);
                position: relative;
                &:hover {
                    cursor: pointer; }
                &:before {
                    background: $green;
                    border-radius: 50%;
                    position: absolute;
                    content: "";
                    top: rem(2px);
                    height: rem(16px);
                    width: rem(16px); }
                &.en:before {
                    left: rem(2px); }
                &.sp:before {
                    right: rem(2px); } } } }

    .btn {
        float: right; } }
