@import url(https://fonts.googleapis.com/css?family=Varela+Round);
@import url(https://fonts.googleapis.com/css?family=Lato:400,400italic);
/* Reset */
*, *:before, *:after {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box; }

html,
body {
  height: 100%;
  position: relative;
  width: 100%; }

img {
  vertical-align: bottom; }

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal; }

ol, ul {
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit; }

input, textarea, select {
  font-size: 100%; }

/* Reset HTML5 Search Input in Webkit */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none; }

input[type=search] {
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none;
  background-color: transparent; }
  a:active, a:hover {
    outline: 0; }
  a:hover {
    cursor: pointer; }

html {
  font-size: 14px; }

body {
  font-family: "Varela Round", Helvetica, Arial, sans-serif; }

*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img {
  height: auto !important;
  max-width: 100%;
  width: auto; }

.wrapper {
  margin: 0 auto;
  width: 100%; }

.wrapper:after,
.row:after {
  content: " ";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  padding: 1em;
  width: 100%; }

@media only screen and (min-width: 20em) {
  .wrapper {
    width: 100%;
    max-width: 1200px; }
  .col-1 {
    width: 8.3333333333%; }
  .col-2 {
    width: 16.6666666667%; }
  .col-3 {
    width: 25%; }
  .col-4 {
    width: 33.3333333333%; }
  .col-5 {
    width: 41.6666666667%; }
  .col-6 {
    width: 50%; }
  .col-7 {
    width: 58.3333333333%; }
  .col-8 {
    width: 66.6666666667%; }
  .col-9 {
    width: 75%; }
  .col-10 {
    width: 83.3333333333%; }
  .col-11 {
    width: 91.6666666667%; }
  .col-12 {
    width: 100%; } }

.btn {
  display: inline-block;
  border: none;
  border-radius: 0.2142857143rem;
  padding: 0.5em 0.8em;
  font-family: +sans("Varela Round");
  font-size: 16px;
  font-size: 1.1428571429rem; }
  .btn:hover {
    cursor: pointer;
    transition: all 0.3s ease; }
  .btn.green {
    background: #1ABC9C;
    color: #FFFFFF; }

@keyframes up-in {
  from {
    transform: translateY(-1rem);
    opacity: 0;
    filter: alpha(opacity=0); }
  to {
    transform: translateY(0);
    opacity: 1;
    filter: alpha(opacity=100); } }

.topbar {
  content: " ";
  display: table;
  clear: both;
  width: 100%; }
  .topbar .languages {
    content: " ";
    display: table;
    clear: both;
    float: left; }
    .topbar .languages .lang {
      float: left;
      text-transform: uppercase; }
    .topbar .languages .switch {
      float: left; }
      .topbar .languages .switch #lang_switch {
        display: none; }
      .topbar .languages .switch label {
        background: #ECF0F1;
        display: inline-block;
        border-radius: 0.7142857143rem;
        text-indent: -99999px;
        height: 1.4285714286rem;
        width: 2.5rem;
        position: relative; }
        .topbar .languages .switch label:hover {
          cursor: pointer; }
        .topbar .languages .switch label:before {
          background: #1ABC9C;
          border-radius: 50%;
          position: absolute;
          content: "";
          top: 0.1428571429rem;
          height: 1.1428571429rem;
          width: 1.1428571429rem; }
        .topbar .languages .switch label.en:before {
          left: 0.1428571429rem; }
        .topbar .languages .switch label.sp:before {
          right: 0.1428571429rem; }
  .topbar .btn {
    float: right; }
