.btn {
    display: inline-block;
    border: none;
    border-radius: rem(3px);
    padding: 0.5em 0.8em;
    font-family: +sans('Varela Round');
    @include font-size(16px);
    &:hover {
        cursor: pointer;
        transition: all 0.3s ease; }
    &.green {
        background: $green;
        color: $white; } }
