@mixin font-size($size) {
    font-size: $size;
    font-size: rem($size); }

@mixin sans($name) {
    font-family: $name, Helvetica, Arial, sans-serif; }

@mixin opacity($opacity) {
    opacity: $opacity;
    $opacity-ie: $opacity * 100;
    filter: alpha(opacity=$opacity-ie); }

@mixin clear {
    content: " ";
    display: table;
    clear: both; }

@mixin shadow {
    box-shadow: 0 0 6px 3px rgba($black, 0.15); }

@mixin heading-size($size, $step-size-heading: 0.3333, $amplifier-heading: 1.2) {
    font-size: $size * $step-size-heading * $amplifier-heading + rem; }

@mixin headings {
    @for $i from 1 through 6 {
        h#{$i} {
            @include heading-size(7 - $i);
            margin: 1.8em 0 0.5em 0;
            line-height: 1.3em;
            color: darken($gray, 15);
            font-family: 'Montserrat', Helvetica, Arial, sans-serif; } } }
